import * as globals from "../globals.js";
import { HandleUrlParameters } from "./handle_parameters.js";
import { ScopeLoaded } from "./scopes.js";
import { SetGlobals } from "./set_globals.js";

// Handle Url Parameters
{
    const url = new URL(globalThis.location.href);
    const params = new URLSearchParams(url.search);
    const mrn = params.get(globals.MRN_PARAM);
    HandleUrlParameters(url, params, mrn);
}
// Set Globals
{
    SetGlobals();
}
// Register Scope Mutation Observer
{
    const observer = new MutationObserver((mutations) => {
        mutations.forEach((mutation) => {
            mutation.addedNodes.forEach((node) => {
                if (node.nodeType === 1 && node.hasAttribute("ng-controller")) {
                    ScopeLoaded(node);
                }
            });
        });
    });
    observer.observe(document.body, { childList: true, subtree: true });
}

/**
 * @description gets all controllers loaded into the DOM
 * @returns {NodeList} NodeList
 */
export function getControllers() {
    return Array.prototype.map.call(
        document.querySelectorAll("[ng-controller]"),
        (/** @type Node */ node) => node.getAttribute("ng-controller"),
    );
}
/**
 * @description returns null if scope doesn't exist otherwise returns scope
 * @param {string} scopeName name of the scope/ng-controller tag
 */
export function doesAngularScopeExist(scopeName) {
    if (!scopeName) {
        return null;
    }
    // Check if Angular is available. If not, return null early.
    if (typeof angular === "undefined" || !angular.element) {
        return null;
    }
    // Use Array.from and then forEach for a more modern iteration approach.
    // Querying all elements can be expensive; consider more targeted queries if possible.
    const allElements = Array.from(document.querySelectorAll("*"));
    for (const el of allElements) {
        const angularElement = angular.element(el);
        // Get the appropriate scope based on the 'isolated' flag.
        const scope = angularElement.scope(); // This gets the scope for the element
        // const isolatedScope = angularElement.isolateScope(); // Use this if you need isolated scopes specifically
        if (scope) {
            // If a scopeName is provided, check if the element's ng-controller attribute matches it.
            if (scopeName) {
                const elementControllerName = el.getAttribute("ng-controller");
                if (elementControllerName === scopeName) {
                    return scope; // Found the scope for the specified controller name.
                }
            } else {
                // If no scopeName is provided, any found scope is sufficient.
                return scope;
            }
        }
    }
    // No matching scope found after checking all elements.
    return null;
}

/**
 * @description inspect an angular scope for unique keys
 * @param {object} scope
 */
export function inspectScope(scope) {
    const result = {};
    for (const key in scope) {
        if (
            Object.prototype.hasOwnProperty.call(scope, key) &&
            !key.startsWith("$") &&
            typeof scope[key] !== "function"
        ) {
            result[key] = scope[key];
        }
    }
    return result;
}

