/**
 * @fileoverview This script injects a global 'gla' object into the window
 * of pages matching the pattern in the manifest.json.
 */
import * as globals from "../globals.js";
import * as utils from "../utils.js";
import { doesAngularScopeExist, getControllers, inspectScope } from "./main.js";

export function SetGlobals() {
    if (typeof globalThis.gla !== "undefined") {
        // Ensure we don't overwrite anything.
        console.error(
            'The "gla" object already exists on the window. Injection skipped.',
        );
        alert(
            "Error: Namespace collision. please notify extension author (EI) to fix.",
        );
        return;
    }
    utils.WriteVerbose('Initializing and injecting the "gla" object.');
    const gla = {
        // version: globals.VERSION,
        // author: "Browser Extension",
        init: new Date().toISOString(),
        scopes: globals.ECW_SCOPES,

        // functions
        doesAngularScopeExist,
        getControllers,
        inspectScope,
    };
    globalThis.gla = gla;
    utils.WriteVerbose('Global "gla" object has been successfully injected.');
}
