import { MRN_PARAM } from "./globals.js";
/** @param {any} messages */
export function WriteVerbose(...messages) {
  console.info("GAAFL:", ...messages);
}
/** @param {Number} ms how long to delay, in milliseconds */
export function delay(ms) {
  return new Promise((res) => setTimeout(res, ms));
}
/** @type {Number} mrn */
export function lumaOpenPatientHub(mrn) {
  globalThis.open(`https://next.lumahealth.io?${MRN_PARAM}=${mrn}`, "_blank");
}
