import { MRN_PARAM } from "../globals.js";

export function HandleUrlParameters(url, params, mrn) {
    if (mrn) {
        params.delete(MRN_PARAM);
        const newUrl =
            url.origin +
            url.pathname +
            (params.toString() ? "?" + params.toString() : "") +
            url.hash;
        globalThis.history.replaceState({}, document.title, newUrl);
    }
    globalThis.addEventListener("load", function () {
        if (mrn) {
            // TODO: add require or load angular scope
            // step
            globalThis
                .getAngularScopeFromControllerName("JellyBeanCountCntrl")
                .openPatientHub(mrn);
        }
    });
}
