/**
 * @fileoverview handles detecting when scopes are added to the page
 * and running callbacks when they are loaded
 */
import { ECW_SCOPES } from "../globals.js";
import { lumaOpenPatientHub } from "../utils.js";
import { Components } from "./components.js";
import { render } from "preact";
function document_view() {
  console.log("document view loaded.");
  const a = document.createElement("div");
  a.innerText = "Open in New Tab";
  document.querySelector("div .fix_footer").appendChild(a);
}
function appointment_modal() {
  const footer = document.querySelector(
    "#newApptDialog > div.modal-content > div.modal-footer",
  );
  const button = footer.appendChild(document.createElement("span"));
  const scope = getAngularScopeFromControllerName(ECW_SCOPES.appointment_modal);
  scope.$apply(function () {
    // console.log("appointment modal loaded");
    /** @type preact.VNode */
    const node = Components[ECW_SCOPES.appointment_modal].openInLumaBtn;
    // const node = Components.HelloWorld;
    node.props.onClick = () => {
      const mrn = SCOPE_UTILS[ECW_SCOPES.appointment_modal].getPatientId();
      lumaOpenPatientHub(mrn);
    };
    // console.log("adding", node, "to", footer);
    // console.log(footer);
    render(node, button);
  });
}
function patient_hub() {
  const parent = document.querySelector("#patient-hubInfo").parentElement;
  const button = parent.appendChild(document.createElement("span"));
  const scope = getAngularScopeFromControllerName(ECW_SCOPES.patient_hub);
  scope.$apply(function () {
    // console.log("appointment modal loaded");
    /** @type preact.VNode */
    const node = Components[ECW_SCOPES.patient_hub].openInLumaBtn;
    // const node = Components.HelloWorld;
    node.props.onClick = () => {
      const mrn = SCOPE_UTILS[ECW_SCOPES.patient_hub].getPatientId();
      lumaOpenPatientHub(mrn);
    };
    // console.log("adding", node, "to", footer);
    // console.log(footer);
    render(node, button);
  });
}
const logController = function (node) {
  console.groupCollapsed(
    `Controller added: ${node.getAttribute("ng-controller")}`,
  );
  console.log("Node:", node);
  console.groupEnd();
};
/*
 * @param {Node} node of the scope detected with the mutation observer
 */
export function ScopeLoaded(node) {
  logController(node);
  const scopeName = node.getAttribute("ng-controller");
  switch (scopeName) {
    case ECW_SCOPES.document_view:
      document_view();
      break;
    case ECW_SCOPES.appointment_modal:
      appointment_modal();
      break;
    case ECW_SCOPES.patient_hub:
      patient_hub();
      break;
  }
}
export const SCOPE_UTILS = {
  [ECW_SCOPES.appointment_modal]: {
    getPatientId: function () {
      const scope = getAngularScopeFromControllerName(
        ECW_SCOPES.appointment_modal,
      );
      return scope.patientId;
    },
  },
  [ECW_SCOPES.patient_hub]: {
    getPatientId: function () {
      const scope = getAngularScopeFromControllerName(ECW_SCOPES.patient_hub);
      return scope.patientIdentifiers.id;
    },
  },
};
